.bar1 {
  transition: 0.4s;
}

.bar2 {
  transition: 0.4s;
}

.bar3 {
  transition: 0.4s;
}

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-11px, 4px);
  transform: rotate(-45deg) translate(-5px, 5px);
  width: 28px !important;
}
.change .bar2 {
  opacity: 0;
}
.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -2px);
  transform: rotate(45deg) translate(-4px, -6px);
  width: 28px !important;
}

